import React, { useState } from 'react';
import CartList from '../components/CartList';
import { useTypedSelector } from '../hooks/useTypedSelector';

interface IOrder {
    name: string,
    phone: number,
    email: string,
    company: string
}

const Cart = () => {
    const { carts } = useTypedSelector(state => state.cart)
    const [ order, setOrder ] = useState<IOrder>({name: '', phone: 0, email: '', company: ''})
    const [nameError, setNameError] = useState('')
    const [phoneError, setPhoneError] = useState('')
    const [emailError, setEmailError] = useState('')
    const [companyError, setCompanyError] = useState('')

    const getSumPrices = (carts: any[]) => {
        let result = carts.reduce(function(sum, current) {
            return sum + current.sum;
          }, 0);

        return result.toFixed(2)
    }

    const postOrder = (e: any, carts: any[], order: IOrder) => {
        e.preventDefault()
        if (validate(order)) {
            console.log(` : ${order.name}`)
            console.log(` : ${order.phone}`)
            console.log(`Email: ${order.email}`)
            console.log(` / : ${order.company}`)
            console.log(` :`)
            carts.map(cart => 
                console.log(`: ${cart.product.name}, : ${cart.count}, : ${cart.sum}`)
            )
            console.log(`: ${getSumPrices(carts)}`)
        }
        setOrder({name: '', phone: 0, email: '', company: ''})
    }

    const validate = (order: IOrder) => {
        let val = true
        if (order.name === '') {
            setNameError('    ')
            val = false
        }
        if (order.phone === 0) {
            setPhoneError('    ')
            val = false
        }
        if (order.email === '') {
            setEmailError('    ')
            val = false
        }
        if (order.company === '') {
            setCompanyError('    ')
            val = false
        }
        return val
    }

    const nameHandler = (e: any) => {
        setOrder({...order, name: e.target.value})
        if (e.target.value === '') {
          setNameError('    ')
        } else {
          setNameError('')
        }
    }

    const phoneHandler = (e: any) => {
        setOrder({...order, phone: e.target.value})
        if (e.target.value === '') {
          setPhoneError('    ')
        } else {
          setPhoneError('')
        }
    }

    const emailHandler = (e: any) => {
        setOrder({...order, email: e.target.value})
        if (e.target.value === '') {
          setEmailError('    ')
        } else {
          setEmailError('')
        }
    }

    const companyHandler = (e: any) => {
        setOrder({...order, company: e.target.value})
        if (e.target.value === '') {
          setCompanyError('    ')
        } else {
          setCompanyError('')
        }
    }

    return (
        <div className='cart'>
            <h1 className="cart_title"></h1>

            <div className="cart_container">

                <section className="cart_wrapper">
                    
                    <div className="cart_message">
                        <img src="./img/alert-triangle_1.svg" className="cart_message-img" alt=" " width="16"/>
                        <p className="cart_message-text">,      .    .</p>
                    </div>

                    <CartList />

                </section>

                <section className="cart_order-wrapper">
                    <h2 className="cart_order-title"></h2>
                    <p className="cart_order-info"> </p>
                    <form action="#" method="post" encType="multipart/form-data" className="cart_order-form">
                        <ul className="cart_order-info-wrapper">
                            <li className="cart_order-info-item">
                                <label htmlFor="username"></label>
                                <div>
                                    <img src="./img/user_1.svg" alt=" " width="14"/>
                                    <input 
                                        type="text" 
                                        name="username" 
                                        placeholder="  " 
                                        className="form-input current"
                                        value={order.name}
                                        onChange={nameHandler}
                                    />
                                </div>
                                {nameError ?  <p className="product_form-error">{nameError}</p> : ''}
                            </li>
                            <li className="cart_order-info-item">
                                <img src="./img/phone_2.svg" alt=" " width="14"/>
                                <input 
                                    type="tel" 
                                    name="phonenumber" 
                                    placeholder=" " 
                                    className="form-input"
                                    value={order.phone}
                                    onChange={phoneHandler}
                                />
                                {phoneError ?  <p className="product_form-error">{phoneError}</p> : ''}
                            </li>
                            <li className="cart_order-info-item">
                                <img src="./img/at-sign_1.svg" alt=" " width="14"/>
                                <input 
                                    type="text" 
                                    name="email" 
                                    placeholder="Email" 
                                    className="form-input"
                                    value={order.email}
                                    onChange={emailHandler}
                                />
                                {emailError ?  <p className="product_form-error">{emailError}</p> : ''}
                            </li>
                            <li className="cart_order-info-item">
                                <img src="./img/briefcase_1.svg" alt=" " width="14"/>
                                <input 
                                    type="text" 
                                    name="org" 
                                    placeholder=" / " 
                                    className="form-input"
                                    value={order.company}
                                    onChange={companyHandler}
                                />
                                {companyError ?  <p className="product_form-error">{companyError}</p> : ''}
                            </li>
                        </ul>
                        
                        <div className="cart_order-total">
                            <p className="cart_order-total-text"></p>
                            <p className="cart_order-total-sum">{getSumPrices(carts)} .</p>
                        </div>

                        <button type="submit" onClick={(e) => postOrder(e, carts, order)} className="cart_order-btn">
                            <img src="./img/shopping.svg" alt=" " width="18"/>
                             
                        </button>

                        <div className="cart_order-commercial">
                            <a href="#">
                                <img src="./img/main/file-text_2.svg" alt="  " width="24"/>
                            </a>
                            <h3> <br/> </h3>
                        </div>
                    </form>
                </section>
            </div>
        </div>
    );
};

export default Cart;