import React, { useState } from 'react';
import GostList from '../components/GostList';
import PriceSlider from '../components/PriceSlider';
import ProductList from '../components/ProductList';
import { useFilters } from '../hooks/useFilters';
import { useTypedSelector } from '../hooks/useTypedSelector';
import { IProduct } from '../types/product';
import { IProductType } from '../types/productType';

const Main = () => {
    const { productTypes } = useTypedSelector(state => state.productType)
    const { products } = useTypedSelector(state => state.product)
    const [productTypeId, setProductTypeId] = useState<number>(0)
    const [gostFilter, setGostFilter] = useState<string[]>([])
    const [minValue, setMinValue] = useState(Math.floor(getMinPrice(products)))
    const [maxValue, setMaxValue] = useState(Math.ceil(getMaxPrice(products)))
    const [priceFilter, setPriceFilter] = useState<{min: number, max: number}>({min: 0, max: 0})
    const filteredProducts = useFilters(products, gostFilter, priceFilter, productTypeId)

    function getMinPrice(products: IProduct[]) {
        if (products.length > 0){
            return products.sort((a,b)=> a.price - b.price)[0].price;
        } else {
            return 0
        }
    }

    function getMaxPrice(products: IProduct[]) {
        if (products.length > 0) {
            return products.sort((a,b)=> b.price - a.price)[0].price;
        } else {
            return 1
        }
        
    }

    function getPriceFilter(min: number, max: number) {
        setPriceFilter({min, max})
        return {min, max}
    }

    const addGostToArray = (checked: boolean, gost: string) => {
        if (checked) {
            setGostFilter([...gostFilter, gost])
        } else {
            setGostFilter(gostFilter.filter((g: string) => g != gost))
        }
    }

    const productTypeIdHandler = (e: any) => {
        setProductTypeId(e.target.value)
      }

    return (
        <main className='main'>
            <section className='main_sort'>
                <p className="main_sort-title">
                    <a href='#'>
                        <img src="./img/arrow_left.svg" alt="" className="main_sort-img" width='11.67' />
                     
                    </a>
                </p>
                <div className="main_sort-wrapper">
                    <p className="main_sort-text"> </p>
                    <a className="main_sort-icon-link" href="#">
                        <img src="./img/sort.svg" alt="" className="main_sort-icon" />
                    </a>
                    <div className="view_wrapper">
                        <a href="#" className='view_link'>
                            <img src="./img/view_icon.svg" alt="" className="view_img" />
                        </a>
                        <a href="#" className='view_link'>
                            <img src="./img/view_list.svg" alt="" className="view_img" />
                        </a>
                    </div>
                </div>
            </section>
            <section className="main_wrapper">
                <aside className="main_filter">
                    <div className="category title">
                        <h2 className="category_title title">
                            <img src="./img/category.svg" alt="" className="category_title-img" />
                            
                        </h2>
                        <ul className="category_list">
                            <li className="category_item">
                                <p className="category_type"> 5.904-1  1:</p>
                                <p className="category_name">
                                      
                                    <span className="category_count">95</span>
                                    </p>
                            </li>
                            <li className="category_item">
                                <p className="category_type"> 4.903-10  4,5,6:</p>
                                <p className="category_name">
                                           
                                    <span className="category_count">15</span>
                                    </p>
                            </li>
                            <li className="category_item">
                                <p className="category_type"> 4.900-9  1: </p>
                                <p className="category_name">
                                               
                                    <span className="category_count">247</span>
                                    </p>
                            </li>
                            <li className="category_item">
                                <p className="category_type"> 3.900-9: </p>
                                <p className="category_name">
                                         
                                    <span className="category_count">2</span>
                                    </p>
                            </li>
                            <li className="category_item">
                                <p className="category_type">8-508.000 - 8-524.000:</p>
                                <p className="category_name">
                                        &lt;89
                                    <span className="category_count">15</span>
                                    </p>
                            </li>
                            <li className="category_item">
                                <p className="category_type">8-138.000 - 8-200.000:</p>
                                <p className="category_name">
                                        
                                    <span className="category_count">247</span>
                                    </p>
                            </li>
                        </ul>
                        <a href="#" className="category_view-all"> </a>
                    </div>
                    <div className="filters">
                        <h2 className="filters_title title">
                            <img src="./img/filter.svg" alt="" className="filters_title-img" />
                            
                        </h2>
                        <ul className="filters_list">
                            <PriceSlider min={minValue} max={maxValue} getPriceFilter={getPriceFilter}/>
                            <li className="filters_item filter">
                                <h3 className="filter_title title">
                                     
                                    <img src="./img/Union.svg" alt="" className="filter_img" />
                                </h3>
                                <select 
                                    value={productTypeId}
                                    onChange={productTypeIdHandler}
                                    className='filter_product-type'
                                >
                                    <option disabled value="0">  </option>
                                    {productTypes.map(option => 
                                        <option key={option.id} value={option.id}>
                                            {option.name}
                                        </option>
                                    )}
                                </select>
                            </li>
                            <li className="filters_item filter">
                                <h3 className="filter_title title">
                                    
                                    <img src="./img/Union.svg" alt="" className="filter_img" />
                                </h3>
                            </li>
                        </ul>
                        <div className='filter_check-wrapper title'>
                            <input type="checkbox" name="filter" value="choice" id="choice"/>
                            <label htmlFor="choice"> </label>
                        </div>
                        <div className='filter_check-wrapper'>
                            <input type="checkbox" name="filter" value="price" id="price"/>
                            <label htmlFor="price"> </label>
                        </div>
                        <div className="filter_button-wrapper">
                            <button className='filter_button'> </button>
                        </div>
                    </div>
                </aside>
                <section className="products">
                    <GostList addGostToArray={addGostToArray} />
                    <ProductList products={filteredProducts}/>
                </section>
            </section>

            <section className="pagination">
                <p className="pagination_text"> </p>
                <ul className="pagination_counter">
                    <li><a href="#" className="item current">9</a></li>
                    <li><a href="#" className="item">15</a></li>
                    <li><a href="#" className="item">21</a></li>
                </ul>
                <ul className="pagination_list">
                    <li><a href="#" className="item">
                        <img src="./img/products/arrow-left.svg" alt=" " width="14"/>
                    </a></li>
                    <li><a href="#" className="item current">1</a></li>
                    <li><a href="#" className="item">2</a></li>
                    <li><a href="#" className="item">3</a></li>
                    <li><a href="#" className="item">4</a></li>
                    <li><a href="#" className="item">5</a></li>
                    <li><a href="#" className="item">
                        <img src="./img/products/arrow-right.svg" alt=" " width="14"/>
                    </a></li>
                </ul>

                <a href="#" className="more">  </a>
            </section>
            <section className="description">
                <h2 className='description_title'>     -    1</h2>
                <p className='description_text'>            .       , , ,      .
                <br/>
                         ,          .</p>
                
                <p className='description_text'>            .
                     17-12, 171-12, 142-12, 092-14, 20, 20  .</p>
                
                <p className='description_text'>       ,       -60C.<br/>
                 ,   ,         
                .</p>
                
                <p className='description_text'>   ,    .      III-18-75,
                 II-23-81.          19537-83   .
                </p>
                <a href="#" className="description_link"> 
                    <img src="./img/main/chevron-up.svg" className="description_link-img" alt=" " width="8"/>
                </a>
            </section>
        </main>
    );
};

export default Main;